//==GENRAL
svg {
    width: 100%;
    height: 100%;
}
a {
    outline: 0;
}
.button{
	border-width: 2px !important;
	&:hover{
		background: $primary-color;
		color: #fff;
		a{
			color: #fff;
		}
	}
}
.noPM{
    padding: 0;
    margin: 0;
}
//Well, as we have a padding 90 for sections, sometimes you have a <p> at the end generated from the CMS.
// What this do is simple, just listens to the last-of-type <p> and overwrite the margin-bottom: 1rem.
p{
    &:last-of-type{
        margin-bottom: 0;
    }
}

//== Overwriting the table-cell for horizontal menu elements

//==Font sizes
//30/24, 18,16, 12
.fsBig,
.fsMed,
.fsSmall,
.leadText,
.sectionLeadTitle{
    -webkit-transition: font-size .22s ease;
            transition: font-size .22s ease;
}
.fsBig,
.sectionLeadTitle{
   font-size: rem-calc(24);
}
.fsMed,
.leadText{
    font-size: rem-calc(14);
}
.fsSmall{
    font-size: rem-calc(11);
}
@include breakpoint (medium){
    .fsBig,
    .sectionLeadTitle{
       font-size: rem-calc(30);
    }
    .fsMed,
    .leadText{
        font-size: rem-calc(18);
    }
    .fsSmall{
        font-size: rem-calc(12);
    }
}

//==Font styles and weights
.black{
    color: $black;
    a{color: $black;}
    &:hover{
        color: lighten($black, 20);
    }
}
.upperCase{
	text-transform: uppercase;
}
.boldFont {
    font-weight: 700;
}
.normalFont {
    font-weight: 400;
}
.lightFont {
    font-weight: 100;
}


//==Section related styles
.sectionSpace{
	padding: rem-calc(40 0 0);
	&.last{
		padding-bottom: rem-calc(40);
	}
    @include breakpoint(medium){
        padding: rem-calc(90 0 0);
        &.last{
            padding-bottom: rem-calc(90);
        }
    }
}


//==OFF-CANVAS
.off-canvas-wrapper {
    overflow-x: visible;
}
.off-canvas-content {
    background: transparent;
}


//==Seamless design for inputs
.formItem {
    &.seamLess {
        input {
            background: transparent;
            border: 0 none;
            box-shadow: 0 0 0 #000;
            color: #fff;
            border-bottom: 1px solid #fff;
        }
    }
}
