//SAME TEMPLATE AS SERVICE PAEG
#sectorDetails{
	.viewOtherSectors{
		max-width: 500px;
		width: 100%;
		font-size: rem-calc(14);
        margin-top: 30px;
        .listHolder{
            column-count: 2;
            column-gap: 2rem;
            .listItem{
               -webkit-column-break-inside: avoid;
                page-break-inside: avoid;
                     break-inside: avoid;
                a{
                    padding-left: 0;
                    &:hover{
                        color: $servicesPageColor;
                    }
                }
            }
        }
	}

	.relatedProject{
		@include breakpoint(small only){
			margin-top: 30px;
		}
		.title{padding-bottom: 10px;}
		.projectItem{
			a{
				position: relative;
				display: block;
				i{
					position: absolute;
					content: '';
					display: block;
					right: 0;
					bottom: 38px;
					width: 50px;
					height: 50px;
					background: #E3E3E3;
					padding: 10px;
					display: none;
				}
			}
            .projectInfo{
                margin-top: 25px;
            }
		}
	}
}

//Slick Slider
.slick-arrow{
	position: absolute;
    right: 0;
    left: auto;
    bottom: auto;
    top: 45%;
    transform: translateY(-100%);
	font-size: 0;
	width: rem-calc(35);
	height: rem-calc(35);
	z-index: 99;
	color: $servicesPageColor;
	fill: $servicesPageColor;
	background: #E3E3E3;
	padding: 0 7px;
	cursor: pointer;
	transition: all .65s ease;
	opacity: 1;
    &[aria-disabled=true]{
        right: 0;
        opacity: 0;
    }
	&.slick-prev{
		transform: rotate(-180deg) translateY(100%);
        right: calc(100% - 35px);
        &[aria-disabled=true]{
            right: 0;
            opacity: 0.2    ;
        }
	}
	&:hover{
		background: $servicesPageColor;
		color: #fff;
	}
}