.where{
	font-size: rem-calc(12);
}
#careerItems{
	&.itemsList{
		.item{
			.readMoreTxt,
			.applyNow{
				padding: 5px 0;
                color: $careersPageColor;
			}
			.applyNow{
				margin-left: rem-calc(15);
				padding-left: rem-calc(10);
				display: inline-block;
				position: relative;
				&:before{
				    content: '';
				    width: 2px;
				    height: 15px;
				    background: #c1c1c2;
				    position: absolute;
				    top: 50%;
				    left: -7px;
				    transform: translateY(-50%);
				}
			}
		}
	}
}


.jobDetails{
	margin-top: rem-calc(10);
	transform: translateY(-20%);
	transition: transform .4s ease, opacity .4s ease;
	opacity: 0;
	height: 0;
	overflow: hidden;
	&.slideMe{
		transform: translateY(0%);
		opacity: 1;
		height: 100%;
		overflow: visible;
	}
}