/* News Page - News Detailed Page
$green-color
Searc Results Page eats from this stylesheet
//.itemsList
================================================== */
.pubDate{
	font-size: rem-calc(12);
}
.itemsList{
	.item{
		padding: rem-calc(30 20);
		.title{
			font-size: rem-calc(18);
			font-weight: 700;
			margin-bottom: 0;
			
		}
		.smallDescription{
			padding-top: 20px;
			line-height: 1.9;
			font-size: rem-calc(18);
			p{
				line-height: 1.9;
				font-size: rem-calc(18);
			}
		}
		.readMoreTxt,
		.applyNow{
			padding: 30px 0;
			display: inline-block;
			text-transform: uppercase;
			font-size: rem-calc(12);
			&:hover{
				color: darken($primary-color, 10);
			}
		}
		&:nth-child(even){
			background: #f4f4f4;
		}
	}
}
.pageContent{
    p{
        margin-bottom: 15px;
    }
    strong{
        color: $newsPageColor;
    }
}
.shareButton{
	padding-bottom: 40px;
	margin-top: rem-calc(40);
	li{
		padding-right: rem-calc(10);
	}
	a{
		width: rem-calc(45);
		height: rem-calc(45);
		background: $primary-color;
		padding: 0 !important;
		&:hover{
			background: darken($primary-color, 10);
		}
	}
	svg{
		width: rem-calc(15);
		height: rem-calc(15);
		fill: #fff;
		color: #fff;
		margin-top: rem-calc(15);
		margin-left: rem-calc(15);
	}
}
