#filtersBoxes{
	padding-top: rem-calc(30);
	.button-group{
		.button{
			margin-bottom: rem-calc(20);
			float: none;
			width: 100%;
			@include breakpoint(medium) {
				margin-right: rem-calc(20);
				margin-bottom: 0;
				float: left;
				width: auto;
			}
			color: $projectsPageColor;
			&:hover,
			&.activeFilter{
				color: #fff;
				background: $projectsPageColor;
			}
		}
	}
}


#filters{
	.filtersContainer{
		border-bottom: 1px solid #ccc;
		.filtersMenu{
			margin-top: 10px;
			width: 100%;
			float: left;
			@include breakpoint(medium){
				width: auto;
				float: right;
			}
		}
		li{
			padding: 10px 15px 14px;
			cursor: pointer;
			outline: none;
			&:first-child{
				padding-left: 0;
			}
			&:hover{
				color: darken($projectsPageColor, 20);
			}
			.subMenu{
				border: 0 none;
				background: $projectsPageColor;
				min-width: 100px;
				text-align: center;
				max-height: 450px;
				overflow-y: auto;
				z-index: 9999999;
				li{
					color: $white;
					padding: 15px 20px;
					&:hover{
						color: darken($white, 20);
					}
				}
				button{
					outline: none;
				}
			}
		}
	}
	.has-submenu{
		background: url(../imgs/SVGs/arrowDown.svg) no-repeat top right;
    background-size: 9px;
    background-position: right 15px;
	}
}

figure{margin: 0}

#projectsList{
	margin-top: rem-calc(30);
	padding-bottom: rem-calc(90);
  overflow: hidden;
	.projectItem{
		margin-bottom: rem-calc(30);
		padding: 0 20px 0 0;
		background: transparent;
		text-align: left;
		a{
			display: block;
			overflow: hidden;
		}
		img{
			margin-bottom: rem-calc(5);
			transition: transform .43s ease;
		}
		.placeDetails{
			margin-top: 15px;
			.title{
				font-size: rem-calc(12);
				margin: 0;
			}
			.tag{
				font-size: rem-calc(18);
				color: $black;
			}
		}
		&:hover{
			img{
				transform: scale(1.1);
			}
			.projectTitle{
				color: $projectsPageColor;
			}
		}
	}
}

#factsSliderSection{
	margin-top: 20px;
	.projectFacts{
		li{
			font-size: rem-calc(16);
			span{
				font-size: rem-calc(15);
			}
		}
	}
	.theSliderHolder{
		@include breakpoint(small only){
			margin-top: rem-calc(20);
			.slick-slide{
				img{width: 100%;}
			}
		}
	}

    // The slider in the single-project page
    // We jut need to make the image there a width: 100%
    // Make it perfect and ask the content to upload the max-size of the image.
    .slick-slide{
        img{
            width: 100%;
        }
    }
	//Slick Slider
	.slick-arrow{
		position: absolute;
		top: 45%;
		left: 15px;
		font-size: 0;
		width: rem-calc(30);
		height: rem-calc(30);
		z-index: 999;
        -webkit-transform: translate(-50%, -50%) rotate(-180deg);
		        transform: translate(-50%, -50%) rotate(-180deg);
		color: $projectsPageColor;
		fill: $projectsPageColor;
		background: #fff;
		padding: 5px 0;
		cursor: pointer;
		opacity: 1;
		&.slick-next{
			left: auto;
			right: -16px;
            -webkit-transform: translate(-50%, -50%);
			        transform: translate(-50%, -50%);
		}
		&:hover{
			background: $projectsPageColor;
			color: #fff;
		}
		&[aria-disabled=true]{
			opacity: 0;
		}
	}
}
#projectInformation{
	background: #f4f4f4;
	margin-top: 50px;
	.info{
		.smallDescription{padding-top: 30px;line-height: 2;}
		.readMoreTxt{
			padding: 15px 0;
			display: inline-block;
			text-transform: uppercase;
			font-size: rem-calc(12);
			color: $singleProjectPageColor;
			font-weight: 700;
		}
	}
}

#projectCreadits{
	margin-top: rem-calc(0);
    padding: rem-calc(0);
	transform: translateY(-100px);
	opacity: 0;
	height: 0;
	overflow: hidden;
	transition: all .43s cubic-bezier(1,-1,0,2);
	.creditList{
		margin-top: rem-calc(15);
		li{font-size: rem-calc(15); margin-top: 15px;}
		.title{
			font-size: rem-calc(14);
			display: block;
			margin-bottom: 0;
		}
	}
	&.showCredits{
		transform: translateY(0);
        padding: rem-calc(40 0);
		opacity: 1;
		height: auto;
		overflow: visible;
        @include breakpoint(medium){
            padding: rem-calc(90 0);
        }
	}
}

#haveProject{
	background: #E8E8E8;
	margin: 0;
	padding: rem-calc(15 0);
	border-top: 1px solid #C7C7C7;
	.haveText{
		padding-top: 15px;
	}
	.button{
		margin-bottom: 0;
	}
}
