/* OffCanvas
================================================== */
#offCanvas{
	overflow-x: hidden;
	-webkit-overflow-scrolling: touch;
	padding-left: rem-calc(60);
	padding-top: rem-calc(130);
	.searchFormContainer{
		.formItem{
			height: rem-calc(37);
			&.withIcon{
				position: relative;
				.ico{
					@include makeIco(15, 15, "true", 0, 14, #fff, "left");
					svg{
						position: absolute;
					}
				}
				input{padding-left: rem-calc(25);}
			}
		}
		//Search Item
		#searchItem{
			.ico{
				pointer-events: all;
				cursor: pointer;
				z-index: 999;
				.openStep{
					display: block;
				}
				.closeStep{
					display: none;
				}
			}
			.inFormItem{
				opacity: 0;
				width: 0;
				transition: all .45s ease-in;
				border-bottom-color: $black;
			}
			&.yoSearch{
				.inFormItem{
					opacity: 1;
					width: 95%;
				}
				.ico{
					width: rem-calc(11);
					height: rem-calc(11);
					.openStep{
						transition: opacity .35s ease .15s;
						display: none;
					}
					.closeStep{
						display: block;
					}
				}
			}
		}
	}
	.mainMenu{margin-top: rem-calc(25);}
	.menu{
		a{
			color: #fff;
			padding-left: 0;
			font-size: rem-calc(16);
			&:hover{
				color: $secondary-color;
			}
		}
		&.darkIt{
			a{
				color: #000;
			}
		}
	}
	.secondaryMenu{
		margin-top: rem-calc(60);
		a{
			font-size: rem-calc(14);
		}
	}
	.socialMenu{
		margin-top: 80px;
		a{height: rem-calc(45); display: block;}
		svg{
			fill: $secondary-color;
			transition: fill .15s ease;
			&:hover{
				fill: $white;
			}
		}
		li{
			&:last-child{
				a{
					width: rem-calc(41);
				}
			}
		}
	}
	//Sub Menu on offcanvas
	.hasSubMenu{
		>a{
			position: relative;
			&:after{
				content: '';
				position: absolute;
				top: 50%;
				right: 20px;
				transform: translateY(-50%);
				background: url(../imgs/SVGs/menuArrow.svg) no-repeat 90% center;
				width: 15px;
				height: 15px;
				background-size: cover;
				cursor-pointer: none;
				transition: all .35s ease;
			}
		}
		.subMenu{
			height: 0;
			opacity: 0;
			overflow: hidden;
			li{
				margin-bottom: rem-calc(0);
			}
			a{
				font-size: rem-calc(15);
				color: #f2f2f2;
				&:hover{
					color: $secondary-color;
				}
			}
		}
		&.opened{
			transition: all .3s ease;
			>a{
				&:after{
					transform: rotate(180deg);
					top: 30%;
				}
			}
			.subMenu{
				height: 100%;
				opacity: 1;
				overflow: visible;
			}
		}
	}

}

.is-open-right{
	-webkit-transform: translateX(0);
			transform: translateX(0);
}
.off-canvas{
	transition: right .35s ease;
	z-index: 999;
	height: 100%;
	&.is-open{
		right: 0;
	}
}

//Open close BurgerMenu
.openCloseMenu{
	width: rem-calc(40);
	height: rem-calc(40);
	position: relative;
	float: right;
	margin-right: 5px;
	z-index: 999;
	outline: none;
	-webkit-transition: transform .36s ease 0s;
	-ms-transition: transform .36s ease 0s;
			transition: transform .36s ease 0s;
	#burgerMenu{
		width: rem-calc(20);
		height: rem-calc(20);
		fill: #fff;
		color: #fff;
		path{
			-webkit-transform-origin: 50%;
			        transform-origin: 50%;
			-webkit-transform: translateY(0) translateX(0) rotate(0);
			        transform: translateY(0) translateX(0) rotate(0);
			display: block;
			transition: transform .65s cubic-bezier(1,-1,0,2), -webkit-transform .65s cubic-bezier(1,-1,0,2);
		}
		#secondLine{
			transition: transform .65s cubic-bezier(1,-1,0,2), opacity .65s cubic-bezier(1,-1,0,2), -webkit-transform .65s cubic-bezier(1,-1,0,2);
		}
	}
	&.opened{
		#firstLine{
			-webkit-transform: translateY(100px) rotate(45deg);
			        transform: translateY(100px) rotate(45deg);
		}
		#thirdLine{
			-webkit-transform: translateY(-90px) rotate(-45deg);
			        transform: translateY(-90px) rotate(-45deg);
		}
		#secondLine{
			opacity: 0;
			-webkit-transform: translateX(-200px);
			        transform: translateX(-200px);
		}
	}
}
.js-off-canvas-exit{
	z-index: 99;
}
