#contactSection{
	iframe{
		width: 100% !important;
	}
	margin-top: rem-calc(50);
	.contactForm{
		font-weight: 100;
		font-size: 15px;
		.wpcf7-form-control{
			border: 1px solid #d5d5d5 !important;
	    box-shadow: 0 0 0 #000 !important;
			width: 85% !important;
		}
	}
	.contactMaps{
		.locationItem:last-child{
			margin-top: rem-calc(50);
		}
		.locationDetails{
			font-size: rem-calc(16);
			.addressDetails{
				margin-bottom: rem-calc(5);
			}
		}
	}
    a{
        color: currentColor;
        font-weight: 100;
        &:hover{
            color: darken($contactPageColor, 10);
        }
    }
}
