.pplSection{
    .keyTitle{
        margin-bottom: 30px;
        font-size: rem-calc(22);
    }
    
    .personBlk{
        margin-bottom: 35px;
        figcaption{
            margin-top: 15px;
        }
        &.noImage{
            border-bottom: 1px solid #ddd;
            padding-bottom: 25px;
            margin-bottom: 25px;
            figcaption{
                margin-top: 0;
            }
        }
    }
}

@include breakpoint(small only){
    #keyPeople{
        padding-top: rem-calc(40);
    }
}

#stickyMenu{
    display: none;
    @include breakpoint(medium){
        display: block;
    }
}
.filterationMenu{
    display: none;
    @include breakpoint(medium){
        display: block;
        padding: rem-calc(25 0);
        background: #fff;
        transition: all .35s ease;
        .menu{
            display: inline-block;
            border-bottom: 2px solid #ccc;
            margin-left: 15px;
            li{
                a{
                    font-size: rem-calc(12);
                    text-transform: uppercase;
                    font-weight: 700;
                    &.active{
                        box-shadow: 0 2px 1px -1px $newsPageColor;
                    }
                }
            }
        }
        &.is-stuck{
            .menu{
                display: block;
                border-bottom-color: #ccc;
            }

        }
    }
}