//Pages Colors
$aboutPageColor: #27cbd3;
$projectsPageColor: #f35878;
$singleProjectPageColor: #ff9d2f;
$servicesPageColor: #8335a4;
$newsPageColor: #45d33c;
$careersPageColor: #ff9e30;
$contactPageColor: #ff9e30;
$peoplePageColor: #8335a4;
/* Body
================================================== */
html,body{
	overflow-x: hidden;
	-webkit-overflow-scrolling: touch;
			overflow-scrolling: touch;
    height: auto;
}
body {
    overflow-x: hidden;
	-webkit-overflow-scrolling: touch;
			overflow-scrolling: touch;
    position: relative;
    -webkit-animation: fadeMe ease-in-out 2.6s both;
    animation: fadeMe ease-in-out 2.6s both;
    opacity: 0;
    font-size: 1.33vw;
    background: #000;
    &.innerPage{
    	background: $white;
    	color: $black;
    }
}




/* GENERAL
================================================== */
@import '_common.scss';
//==MIXINS
//1- SVG icon
@mixin makeIco ($w, $h, $positioned, $x, $y, $fill, $direction) {
    pointer-events: none;
    width: rem-calc($w);
    height: rem-calc($h);
    @if($positioned=="true") {
        position: absolute;
        top: rem-calc($y);
        #{$direction}: rem-calc($x);
    }
    svg {
        fill: $fill;
    }
}


/* A Theming mixins
================================================== */
@mixin hollowBtn($pageName, $colorVarName) {

  ##{$pageName} {
    //=== CurrentColor
      .currentColor {
          color: $colorVarName !important;
          fill: $colorVarName !important;
      }
    //===Buttons (Hollow One)
    .hollow.button{
        color: $colorVarName;
        border-color: $colorVarName;
        &:hover {
            color: #fff;
            background: $colorVarName;
            border-color: $colorVarName;
        }
    }

    //=== Burger Menu
    .btn-menu {
        display: block;
        width: 0;
        height: 0;
        position: relative;
        margin: 0;
        padding: 22px;
        cursor: pointer;
        background: #fff;
        color: $colorVarName;
        span {
            width: 21px;
            height: 3px;
            color: $colorVarName;
            background-color: $colorVarName;
            font-size: 0;
            position: absolute;
            top: 20px;
            left: 11px;
        }
        span:before,
        span:after {
            content: '';
            position: absolute;
            width: 100%;
            height: 100%;
            left: 0;
            background-color: $colorVarName;
            -webkit-transition: all .3s ease;
            transition: all .3s ease;
            -webkit-transform: translateY(-6px);
            transform: translateY(-6px);
        }
        span:after {
            -webkit-transform: translateY(6px);
            transform: translateY(6px);
        }
        &:hover{
            color: darken( $colorVarName, 20 );
        }
}
    .btn-menu-open span {
        background-color: transparent;
        &:before {
            -webkit-transform: translateY(0) rotate(45deg);
            transform: translateY(0) rotate(45deg);
        }
        &:after {
            -webkit-transform: translateY(0) rotate(-45deg);
            transform: translateY(0) rotate(-45deg);
        }
}
  }
}

$themes: (
    home: $primary-color,
    aboutPage: $aboutPageColor,
    projectsPage: $projectsPageColor,
    projectPage: $singleProjectPageColor,
    servicePage: $servicesPageColor,
    listingTemplate: $newsPageColor,
    singleArticleTemplate: $newsPageColor,
    careersPage: $careersPageColor,
    contactPage: $contactPageColor,
    peoplePage: $peoplePageColor
) !default;

//Loop the themes
@each $pageName, $colorRef in $themes {
  @include hollowBtn($pageName, $colorRef);
}




