/* HomePage
================================================== */

// ___ Header _____
header {
	background: $black;
    #topHeader{
    	padding: rem-calc(45 0);
    	position: relative;
    }
    .logo {
        width: rem-calc(100);
        height: rem-calc(100);
        svg {
            fill: $white;
        }
    }
    .tagLine {
        font-size: rem-calc(14);
        padding-top: rem-calc(35);
        @include breakpoint(small only){
        	text-align: center;
        }
    }
    .menuController{
		position: absolute;
		right: 0;
		top: 70px;
		z-index: 9999999;
    }
}

// ___Hero Section_____

#heroSection{
	padding: rem-calc(120 0);
	background: url(../imgs/hero.jpg) no-repeat center top;
	background-size: cover !important;
	.heroDetails{
		color: $black;
		h1,
		.description{
			text-rendering: geometricPrecision;
		}
		h1{
			font-size: calc((7px + 0.5vw) * 4.235);
		}
		.description{
			font-size: calc((10px + 0.5vw) * 1.618);
			max-width: calc((10px + 0.5vw) * 35);
		}
		.heroActions{
			margin-top: rem-calc(35);
			a{
				margin-right: rem-calc(10);
				background: transparent;
				color: $black;
				border: 2px solid $black;
				text-transform: uppercase;
				position: relative;
				z-index: 2;
				@include breakpoint (medium){
					margin-right: rem-calc(30);
				}
				&:after{
					content: '';
					position: absolute;
					top: 0;
					left:0;
					background-color: $black;
					width: 100%;
					height: 0%;
					transition: height .35s ease;
					z-index: -1;
				}
				&.greenBtn{
					border-color: $primary-color;
					color: $primary-color;
					&:after{
						background-color: $primary-color;
					}
				}
				&:hover{
					color: $white;
					z-index: 2;
					&:after{
						height: 100%;
					}
				}
			}
		}
	}

	@include breakpoint(medium){
		padding: rem-calc(240 0);
	}
}

// ___Facts and Numbers_____

#factsNumbers{
	padding: rem-calc(40 0);
	@include breakpoint(medium){
		padding: rem-calc(90 0);
	}
	.numbersBoxes{
		margin-bottom: 40px;
		.numberBox{
			text-align: center;
			font-size: calc((7px + 0.5vw) * 4.235);
			margin-top: rem-calc(40);
			span{
				display: block;
				font-size: rem-calc(12);
				max-width: 70%;
				margin: 0 auto;
				@include breakpoint(medium){
					font-size: rem-calc(14);
				}
			}
		}
	}
	.fact{
		position: relative;
	}
	.factImg{
		width: 100%;
		@include breakpoint(medium){
			width: 380px;
		}
	}
	.factText{
	    position: relative;
	    transform: translateY(0);
	    right: 0;
	    width: 100%;
	    margin-top: 20px;
		font-size: calc((10px + 0.5vw) * 1.45);
		@include breakpoint(medium){
			position: absolute;
			top: 50%;
			right: 10px;
			transform: translateY(-50%);
			width: 70%;
		}
	}
}

.actionBlk{
	padding-top: rem-calc(35);
	a{
		font-size: rem-calc(12);
		color: $primary-color;
		text-decoration: underline;
	}
	@include breakpoint (small only){
		text-align: center;
		padding-top: 0;
	}
}
#jlaMore{
	padding-bottom: rem-calc(20);
	.jlaSectors{
		.sectorsMnu{
			width: 40%;
			float: left;
			margin-right: 4%;
			border-right: 1px solid $secondary-color;
			@include breakpoint (medium){
				width: 22%;
			}
			a{
				font-size: rem-calc(14);
				padding-left: 0;
			}
			li.active{
				a{color: $primary-color;}
			}
		}
		.sectorContents{
			position: relative;
			min-height: 170px;
			overflow: hidden;
			width: 55%;
			@include breakpoint (medium){
				width: 50%;
			}
			article{
				position: absolute;
				background: $black;
				top: 30%;
				left: 0;
				opacity: 0;
				transform: translateY(200px);
				transition: transform .35s ease, opacity .1s ease;
				font-size: rem-calc(15);
				@include breakpoint (medium){
					font-size: rem-calc(28);
				}
				&.showIt{
					opacity: 1;
					transform: translateY(-50%);
					transition: transform .35s ease, opacity .8s ease-in;
				}
			}
		}
	}
}

#ourPlacesSection{
	background: url(../imgs/placesBG.jpg) no-repeat 0 0;
	background-size: cover;
}
#ourPlaces{
	padding: rem-calc(60 0);
	.sectionIntro{
		@include breakpoint(small only){
			padding-bottom: rem-calc(40);
		}
		.title{
			font-size: rem-calc(30);
		}
		.description{
			font-size: rem-calc(18);
			@include breakpoint(medium){
				max-width: 70%;
			}
		}
	}
}
.ourPlacesSlider{
	padding-bottom: rem-calc(50);
	@include breakpoint(medium){
		padding-bottom: rem-calc(150);
	}
	.place{
		padding: rem-calc(0 15);
		.placeDetails{
			margin-top: 15px;
			max-width: 85%;
			.title{
				font-size: rem-calc(12);
				margin: 0;
			}
			.tag{
				font-size: rem-calc(18);
			}
		}
	}
	.slick-dots{
	    display: block;
	    position: absolute;
	    left: 50%;
	    transform: translateX(-50%);
	    bottom: 65px;
	    overflow: hidden;
		li{
		    list-style-type: none;
		    font-size: 0;
		    width: 4px;
		    height: 25px;
		    display: inline-block;
		    background: #fff;
		    margin-right: 5px;
		    transform: translateY(10px);
		    transition: transform .35s ease .1s;
		    cursor: pointer;
		    background: rgba(131, 131, 131, 0.47);;
		    &.slick-active{
				transform: translateY(0);
				background: $primary-color;
		    }
		}
	}
	.slick-arrow{
		left: 40%;
		bottom: 65px;
        top: auto;
		background: transparent;
		width: rem-calc(45);
		height: rem-calc(45);
		color: $white;
		fill: $white;
		&.slick-prev{
			-webkit-transform: rotate(-180deg) translateX(-50%);
					transform: rotate(-180deg) translateX(-50%);
		}
		&.slick-next{
			right: 40%;
            left: auto;
			-webkit-transform: translateX(50%);
					transform: translateX(50%);
		}
		&:hover{
			background: transparent;
			svg{
				fill: $primary-color;
				color: $primary-color;
			}
		}
	}
}

// ___Footer_____
footer{
	background: $black;
}
#footer{
	padding: rem-calc(35 0);
	a{
		font-size: rem-calc(13);
		position: relative;
		@include breakpoint (large){
			&:after{
				content: '';
				position: absolute;
				bottom: 0;
				left: 20px;
				width: 0%;
				height: 3px;
				background: $white;
				transition: width .19s ease;
			}
			&:hover{
				&:after{
					width: 45%;
				}
			}
		}
	}
	@include breakpoint (medium down){
		ul{
			float: none;
			width: 100%;
		}
		li{
			width: 40%;
			display: inline-block;
			&.terms{width: 100%;}
		}
	}
}


